html {
  font-family: Roboto, sans-serif;
  font-weight: 500;
}

body {
  --color-active: #00acdc;
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header_logo {
  font-size: 0;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
}

.nav {
  justify-content: space-between;
  display: flex;
}

.nav__item {
  text-align: center;
  margin: 0;
  padding-right: 20px;
}

.nav__item:first-child {
  margin-left: 0;
}

.nav__item:last-child {
  padding-right: 0;
}

.nav__link {
  text-transform: uppercase;
  color: #000;
  height: 60px;
  font-size: 12px;
  line-height: 60px;
  text-decoration: none;
  display: block;
  position: relative;
}

.is-active, .nav__link:hover {
  color: var(--color-active);
}

.is-active:after {
  content: "";
  background-color: var(--color-active);
  opacity: 1;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}
